import Vue from 'vue'
import Router from 'vue-router'

import login from '@/components/login/login'  // 

import rolePermissions from '@/components/staff/role-permissions/role-permissions'  // 
import staffList from '@/components/staff/list/list'   // 

import userList from '@/components/user/list/list'   // 
import reReferee from '@/components/user/re-referee/re-referee'   // 
import changeMobile from '@/components/user/change-mobile/change-mobile'   // 
import userDetail from '@/components/user/detail/detail'   // 
import userInfo from '@/components/user/detail/info'   // 
import userCapital from '@/components/user/detail/capital'   // 
import userLend from '@/components/user/detail/lend'   // 
import userBorrow from '@/components/user/detail/borrow'   // 

import organization from '@/components/user/organization/organization'   // 

import product from '@/components/product/product'   // 
import project from '@/components/project/project'   // 

import bidList from '@/components/bid/list/list'   // 
import apply from '@/components/bid/apply/apply'   // 
import needDispatch from '@/components/bid/need-dispatch/need-dispatch'   // 
import alreadyDispatch from '@/components/bid/already-dispatch/already-dispatch'   // 
import ongoing from '@/components/bid/ongoing/ongoing'   // 
import fullReview from '@/components/bid/full-review/full-review'   // 
import generateContract from '@/components/bid/generate-contract/generate-contract'   // 
import flow from '@/components/bid/flow/flow'   // 
import cancle from '@/components/bid/cancle/cancle'   // 

import repaymentToday from '@/components/repayment/today/today'   // 
import notDue from '@/components/repayment/not-due/not-due'   // 
import payOff from '@/components/repayment/pay-off/pay-off'   // 
import overDue from '@/components/repayment/over-due/over-due'   // 
import breakContract from '@/components/repayment/break-contract/break-contract'   // 

import zjbdmx from '@/components/reconciliation/sub/zjbdmx'   // --
import zjye from '@/components/reconciliation/sub/zjye'   // --
import zjyemx from '@/components/reconciliation/sub/zjyemx'   // --
import bdzhye from '@/components/reconciliation/sub/bdzhye'   // --
import bdxx from '@/components/reconciliation/sub/bdxx'   // --
import bdtzmx from '@/components/reconciliation/sub/bdtzmx'   // --
import hkmx from '@/components/reconciliation/sub/hkmx'   // --
import czdd from '@/components/reconciliation/sub/czdd'   // --
import ddzt from '@/components/reconciliation/sub/ddzt'   // --
import ptkhbh from '@/components/reconciliation/sub/ptkhbh'   // --

import threePayment from '@/components/capital/three-payment/three-payment'   // 
import platformFund from '@/components/capital/platform-fund/platform-fund'   // 
import merchantsToUser from '@/components/capital/merchantsToUser/merchantsToUser'   // 
import platChild from '@/components/capital/plat-child/plat-child'   // 

import sInvitation from '@/components/statistics/invitation/invitation'   // 

import cmsPic from '@/components/cms/pic/pic'   // 
import cmsArtical from '@/components/cms/artical/artical'   // 
import newArtical from '@/components/cms/new-artical/new-artical'   // 
import column from '@/components/cms/column/column'   // 

import mShort from '@/components/message/short/short'   // 
import mService from '@/components/message/service/service'   // 

import modifyPassword from '@/components/system/modify-password/modify-password'   // 

Vue.use(Router)

let rt = new Router({
  mode: 'history',
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      component: login
    },
    {
      path: '/role-permissions',
      component: rolePermissions
    },
    {
      path: '/staff-list',
      component: staffList
    },
    {
      path: '/user-list',
      component: userList
    },
    {
      path: '/user-detail',
      component: userDetail,
      redirect: '/user-detail/info',
      children: [
        {
          path: '/user-detail/info',
          component: userInfo
        },
        {
          path: '/user-detail/capital',
          component: userCapital
        },
        {
          path: '/user-detail/lend',
          component: userLend
        },
        {
          path: '/user-detail/borrow',
          component: userBorrow
        }
      ]
    },
    {
      path: '/organization',
      component: organization
    },
    {
      path: '/re-referee',
      component: reReferee
    },
    {
      path: '/change-mobile',
      component: changeMobile
    },
    {
      path: '/product',
      component: product
    },
    {
      path: '/project',
      component: project
    },
    {
      path: '/bid-list',
      component: bidList
    },
    {
      path: '/apply',
      component: apply
    },
    {
      path: '/need-dispatch',
      component: needDispatch
    },
    {
      path: '/already-dispatch',
      component: alreadyDispatch
    },
    {
      path: '/ongoing',
      component: ongoing
    },
    {
      path: '/full-review',
      component: fullReview
    },
    {
      path: '/generate-contract',
      component: generateContract
    },
    {
      path: '/flow',
      component: flow
    },
    {
      path: '/cancle',
      component: cancle
    },
    {
      path: '/repayment-today',
      component: repaymentToday
    },
    {
      path: '/not-due',
      component: notDue
    },
    {
      path: '/pay-off',
      component: payOff
    },
    {
      path: '/over-due',
      component: overDue
    },
    {
      path: '/break-contract',
      component: breakContract
    },
    {
      path: '/zjbdmx',
      component: zjbdmx
    },
    {
      path: '/zjye',
      component: zjye
    },
    {
      path: '/zjyemx',
      component: zjyemx
    },
    {
      path: '/bdzhye',
      component: bdzhye
    },
    {
      path: '/bdxx',
      component: bdxx
    },
    {
      path: '/bdtzmx',
      component: bdtzmx
    },
    {
      path: '/hkmx',
      component: hkmx
    },
    {
      path: '/czdd',
      component: czdd
    },
    {
      path: '/ddzt',
      component: ddzt
    },
    {
      path: '/ptkhbh',
      component: ptkhbh
    },
    {
      path: '/three-payment',
      component: threePayment
    },
    {
      path: '/platform-fund',
      component: platformFund
    },
    {
      path: '/merchants-to-user',
      component: merchantsToUser
    },
    {
      path: '/plat-child',
      component: platChild
    },
    {
      path: '/s-invitation',
      component: sInvitation
    },
    {
      path: '/cms-pic',
      component: cmsPic
    },
    {
      path: '/cms-artical',
      component: cmsArtical
    },
    {
      path: '/new-artical',
      component: newArtical
    },
    {
      path: '/column',
      component: column
    },
    {
      path: '/m-short',
      component: mShort
    },
    {
      path: '/m-service',
      component: mService
    },
    {
      path: '/modify-password',
      component: modifyPassword
    }
  ]
})

rt.beforeEach((to, from, next) => {
  next()
})

export default rt



