<template>
<div class="project-detail">
    <p @click="goback" class="goback"></p>
    <p class="head">
        <span>{{ project.title }}</span>
    </p>
    <div>
        <table>
            <tbody>
                <tr>
                    <th>id</th>
                    <td>{{ project.id }}</td>
                    <th></th>
                    <td>{{ project.productName }}</td>
                    <th></th>
                    <td>{{ project.amount }}</td>
                </tr>
                <tr>
                    <th>ID</th>
                    <td>{{ project.guaranteeId }}</td>
                    <th></th>
                    <td>{{ project.guaranteeRealm }}</td>
                    <th></th>
                    <td>{{ project.months }}</td>
                </tr>
                <tr>
                    <th></th>
                    <td>{{ project.rate }}</td>
                    <th></th>
                    <td>{{ project.method |  method }}</td>
                    <th></th>
                    <td>{{ project.groupId ? '' : '' }}</td>
                </tr>
                <tr>
                    <th></th>
                    <td>{{ project.minAmount }}</td>
                    <th></th>
                    <td>{{ project.maxAmount }}</td>
                    <th></th>
                    <td>{{ project.stepAmount }}</td>
                </tr>
                <tr>
                    <th></th>
                    <td>{{ project.loanGuaranteeFee }}</td>
                    <th></th>
                    <td>{{ project.loanServiceFee }}</td>
                    <th></th>
                    <td>{{ project.loanRiskFee }}</td>
                </tr>
                <tr>
                    <th></th>
                    <td>{{ project.loanManageFee }}</td>
                    <th></th>
                    <td>{{ project.loanInterestFee }}</td>
                    <th></th>
                    <td>{{ project.investInterestFee }}</td>
                </tr>
            </tbody>
        </table>
    </div>
    <p class="bid-detail-p"><span>{{ project.firmInfo }}</span></p>
    <p class="bid-detail-p"><span>{{ project.operationRange }}</span></p>
    <p class="bid-detail-p"><span>{{ project.description }}</span></p>
    <p class="bid-detail-p"><span>{{ project.repaySource }}</span></p>
    <p class="bid-detail-p"><span>{{ project.riskInfo }}</span></p>
    <p>
        <!-- <img src="../../img/errorBg.png" alt="" />
        <img src="../../img/errorBg.png" alt="" />
        <img src="../../img/errorBg.png" alt="" />
        <img src="../../img/iconBg.gif" alt="" /> -->
    </p>
    <div class="search-bar">
        <ul class="nav">
            <li class="active"></li>
        </ul>
        <selection :index-default="statusIndex" :arr="projectStatus" @selected="statusFn" keystr="name"></selection>
        <div><span>{{ totalAmount }}</span> </div>
    </div>
    <table>
        <thead>
            <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="item in loans">
                <td>{{ item.title }}</td>
                <td>{{ item.serial }}</td>
                <td>{{ item.amount }}</td>
                <td>{{ item.bidAmount }}</td>
                <td>{{ item.months }}</td>
                <td>{{ item.method | method }}</td>
                <td>{{ item.status | status }}</td>
                <td><span v-if="item.timeOpen">{{ item.timeOpen | date }}</span></td>
                <td><span v-if="item.timeSettled">{{ item.timeSettled | date }}</span></td>
            </tr>
            <tr v-if="!loans.length">
                <td colspan="9"></td>
            </tr>
        </tbody>
    </table>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import selection from '../submodel/selection.vue'
import alert from '../submodel/alert.vue'
export default {
    name: 'project-detail',
    props: {
        dataObj: {
            type: Object,
            default: function () {
                return {}
            }
        }
    },
    data () {
        return {
            projectStatus: [
                {
                    name: '',
                    val: 'ALL'
                },
                {
                    name: '',
                    val: 'INITIATED'
                },
                {
                    name: '',
                    val: 'UNPLANED'
                },
                {
                    name: '',
                    val: 'SCHEDULED'
                },
                {
                    name: '',
                    val: 'OPENED'
                },
                {
                    name: '',
                    val: 'FAILED'
                },
                {
                    name: '',
                    val: 'FINISHED'
                },
                {
                    name: '',
                    val: 'CANCELED'
                },
                {
                    name: '',
                    val: 'SETTLED'
                },
                {
                    name: '',
                    val: 'CLEARED'
                },
                {
                    name: '',
                    val: 'OVERDUE'
                },
                {
                    name: '',
                    val: 'BREACH'
                },
                {
                    name: '',
                    val: 'ARCHIVED'
                }
            ],
            statusIndex: 0,
            project: {},
            loans: [],
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            totalAmount: 0
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    components: {
        selection,
        alert
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/project/searchProjectByParams', {
                project: {
                    id: this.dataObj.id
                },
                status: this.projectStatus[this.statusIndex].val
            }, function (res) {
                if (res.data.message.code === 1000) {
                    This.project = res.data.project
                    This.loans = res.data.loans
                    This.totalAmount = res.data.totalAmount
                } else {
                    This.msg.content = res.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        statusFn (index) {
            this.statusIndex = index
            this.getData()
        },
        closeMsg () {
            this.msg.isShow = false
        },
        goback () {
            this.$emit('backFn')
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.search-bar {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    line-height: 40px;
    ul {
        margin-right: 30px;
        .active {
            padding: 0 30px;
            background-color: #75bcec;
            color: #fff;
        }
    }
}
.goback {
    display: inline-block;
    padding: 4px 30px;
    color: #5ea7ea;
    cursor: pointer;
    font-size: 16px;
}
</style>



