<template>
<div class="bid-detail">
    <p @click="goback" class="goback"></p>
    <p class="head">
        <span>{{ dataList.loan.title }}</span>
    </p>
    <div>
        <table>
            <tbody>
                <tr>
                    <th>id</th>
                    <td>{{ dataList.loan.id }}</td>
                    <th></th>
                    <td>{{ dataList.loan.productName }}</td>
                    <th></th>
                    <td>{{ dataList.loan.amount }}</td>
                </tr>
                <tr>
                    <th>ID</th>
                    <td>{{ dataList.loan.guaranteeId }}</td>
                    <th></th>
                    <td>{{ dataList.loan.guaranteeRealm }}</td>
                    <th></th>
                    <td>{{ dataList.loan.months }}</td>
                </tr>
                <tr>
                    <th></th>
                    <td>{{ dataList.loan.rate }} <span class="add-rate" v-if="dataList.loan.addRate"> + {{ dataList.loan.addRate }}</span> </td>
                    <th></th>
                    <td>{{ dataList.loan.method |  method }}</td>
                    <th></th>
                    <td>{{ dataList.loan.groupId ? '' : '' }}</td>
                </tr>
                <tr>
                    <th></th>
                    <td>{{ dataList.loan.minAmount }}</td>
                    <th></th>
                    <td>{{ dataList.loan.maxAmount }}</td>
                    <th></th>
                    <td>{{ dataList.loan.stepAmount }}</td>
                </tr>
                <tr>
                    <th></th>
                    <td>{{ dataList.loan.loanGuaranteeFee }}</td>
                    <th></th>
                    <td>{{ dataList.loan.loanServiceFee }}</td>
                    <th></th>
                    <td>{{ dataList.loan.loanRiskFee }}</td>
                </tr>
                <tr>
                    <th></th>
                    <td>{{ dataList.loan.loanManageFee }}</td>
                    <th></th>
                    <td>{{ dataList.loan.loanInterestFee }}</td>
                    <th></th>
                    <td>{{ dataList.loan.investInterestFee }}</td>
                </tr>
            </tbody>
        </table>
    </div>
    <p class="bid-detail-p"><span>{{ dataList.projectDtail.project.firmInfo }}</span></p>
    <p class="bid-detail-p"><span>{{ dataList.projectDtail.project.operationRange }}</span></p>
    <p class="bid-detail-p"><span>{{ dataList.projectDtail.project.description }}</span></p>
    <p class="bid-detail-p"><span>{{ dataList.projectDtail.project.repaySource }}</span></p>
    <p class="bid-detail-p"><span>{{ dataList.projectDtail.project.riskInfo }}</span></p>
    <p>
        <!-- <img src="../../img/errorBg.png" alt="" />
        <img src="../../img/errorBg.png" alt="" />
        <img src="../../img/errorBg.png" alt="" />
        <img src="../../img/iconBg.gif" alt="" /> -->
    </p>
    <div>
        <ul class="nav">
            <li @click="navIndex=0" :class="{active: navIndex===0}"></li>
            <li @click="navIndex=1" :class="{active: navIndex===1}"></li>
        </ul>
        <table v-if="navIndex===0">
            <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-if="dataList.loanRepayments.length" v-for="item in dataList.loanRepayments">
                    <td>{{ item.currentPeriod }}</td>
                    <td>{{ item.dueDate }}</td>
                    <td>{{ item.amountPrincipal + item.amountInterest }}</td>
                    <td>{{ item.status | repayStatus}}</td>
                </tr>
                <tr v-if="!dataList.loanRepayments.length">
                    <td colspan="4"></td>
                </tr>
            </tbody>
        </table>
        <table v-if="navIndex===1">
            <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-if="dataList.invests.length" v-for="item in dataList.invests">
                    <td>{{ item.userName }}</td>
                    <td>{{ item.amount }}</td>
                    <td>{{ item.submitTime | date }}</td>
                    <td>{{ item.status | investStatus }}</td>
                </tr>
                <tr v-if="!dataList.invests.length">
                    <td colspan="4"></td>
                </tr>
            </tbody>
        </table>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
export default {
    name: 'bid-detail',
    props: {
        dataObj: {
            type: Object,
            default: function () {
                return {}
            }
        }
    },
    data () {
        return {
            dataList: {
                invests: [],
                loan: {},
                loanRepayments: [],
                product: {},
                projectDtail: {
                    project: {}
                }
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            navIndex: 0
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/loan/searchLoanByParams', {
                loan: {
                    id: this.dataObj.id
                }
            }, function (res) {
                if (res.data.message.code === 1000) {
                    This.dataList = res.data.loanDetail
                } else {
                    This.msg.content = res.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        goback () {
            this.$emit('backFn')
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.head span {
    display: inline-block;
    padding: 4px 20px;
    color: #333;
    font-weight: bold;
}
.goback {
    display: inline-block;
    padding: 4px 30px;
    color: #5ea7ea;
    cursor: pointer;
    font-size: 16px;
}
.bid-detail-p {
    line-height: 26px;
    padding: 0 6px;
    margin:10px 0;
    font-size: 16px;
    color:#333;
    font-weight: bold;
}
.bid-detail-p span {
    font-size: 14px;
    font-weight:normal;
    color:#666;
    word-wrap:break-word;
}
.nav {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .active {
        background-color: #75bcec;
        color: #fff;
    }
    li {
        display: block;
        padding: 6px 30px;
        font-size: 16px;
        cursor: pointer;
    }
}
.add-rate {
    color: #ff6f0a;
    font-size: 12px;
}
</style>



