<template>
<div class="zjbdmx">
    <div class="head">
        <input v-model="parame.order_no" type="text" placeholder="">&nbsp;&nbsp;
        <input v-model="parame.mobile" type="text" placeholder="" size="26">&nbsp;&nbsp;
        <selection class="inline-block" :index-default="accountTypeIndex" :arr="accountTypeList" @selected="accountTypeFn"></selection>
        <selection class="inline-block" :index-default="transIndex" :arr="trans" @selected="transFn"></selection>
        <input v-model="parame.now" type="text" placeholder="" size="2">&nbsp;&nbsp;
        <v-search class="search-bar" @searchFn="searchFn" @changeVal="searchValChange"></v-search>
    </div>
    <div>
        <table>
            <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in list">
                    <td>{{ item.plat_no }}</td>
                    <td>{{ item.platcust }}</td>
                    <td>{{ item.amt_type }}</td>
                    <td>{{ item.amt }}</td>
                    <td>{{ item.trans_date + ' ' + item.trans_time }}</td>
                </tr>
                <tr v-if="!list.length && parame.queryEnd">
                    <td colspan="5"></td>
                </tr>
            </tbody>
        </table>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import search from '../../submodel/search'
import selection from '../../submodel/selection.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'zjbdmx',
    data () {
        return {
            list: [],
            accountTypeList: [
                {
                    lable: '',
                    val: ''
                },
                {
                    lable: ' ',
                    val: '1'
                },
                {
                    lable: '',
                    val: '3'
                },
                {
                    lable: '',
                    val: '4'
                },
                {
                    lable: '',
                    val: '5'
                },
                {
                    lable: '',
                    val: '6'
                },
                {
                    lable: '',
                    val: '7'
                },
                {
                    lable: '',
                    val: '9'
                },
                {
                    lable: '',
                    val: '10'
                },
                {
                    lable: '',
                    val: '11'
                }
            ],
            accountTypeIndex: 0,
            trans: [
                {
                    lable: '',
                    val: ''
                },
                {
                    lable: '',
                    val: ''
                },
                {
                    lable: '',
                    val: ''
                },
                {
                    lable: '',
                    val: ''
                },
                {
                    lable: '',
                    val: ''
                },
                {
                    lable: '',
                    val: ''
                },
                {
                    lable: '',
                    val: ''
                },
                {
                    lable: '',
                    val: ''
                },
                {
                    lable: '',
                    val: ''
                },
                {
                    lable: '',
                    val: ''
                },
                {
                    lable: '',
                    val: ''
                },
                {
                    lable: '',
                    val: ''
                },
                {
                    lable: '',
                    val: ''
                }
            ],
            transIndex: 0,
            parame: {
                now: 1,
                startTime: '',
                endTime: '',
                order_no: '',
                mobile: '',
                queryEnd: false
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    methods: {
        getData () {
            let This = this
            if (!this.parame.mobile || !this.parame.now) {
                if (!this.parame.mobile) {
                    This.msg.content = ''
                } else if (!this.parame.now) {
                    This.msg.content = ''
                }
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/search/seachPlatcustFundDetail', {
                'order_no': this.parame.order_no,
                'platcust': this.parame.mobile,
                'start_date': this.parame.startTime,
                'end_date': this.parame.endTime,
                'trans_name': this.trans[this.transIndex].val,
                'pagenum': this.parame.now,
                'acct_type': this.accountTypeList[this.accountTypeIndex].val
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.list = data.data.list || []
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
                This.parame.queryEnd = true
            }, function (err) {
                console.log(err)
                This.parame.queryEnd = true
            })
        },
        searchFn () {
            this.getData()
        },
        searchValChange (start, end) {
            this.parame.startTime = start
            this.parame.endTime = end
        },
        accountTypeFn (index) {
            this.accountTypeIndex = index
        },
        transFn (index) {
            this.transIndex = index
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        'v-search': search,
        selection,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.head {
    padding: 10px 0;
}
.btn {
    display: inline-block;
    padding: 6px 20px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
.search-bar {
    display: inline-block;
    margin-left: 10px;
}
</style>



