<template>
<div class="new-bid">
    <span class="backpage" @click="goback"></span class="back">
    <h3>{{ title }}</h3>
    <div class="row">
        <p class="dis"></p>
        <div>
            <input type="text" v-model="dataList.title" />
            <span>+</span>
        </div>
        
    </div>
    <div class="row">
        <p class="dis"></p>
        <div>
            <input type="text" v-model="dataList.serial" />
            <span>DBS-20150831-021-084</span>
        </div>
    </div>
    <div class="row">
        <p class="dis"></p>
        <span>{{ dataList.amount }}</span>
    </div>
    <div class="row">
        <p class="dis"></p>
        <span>{{ dataList.surplusAmount }}</span>
    </div>
    <div class="row">
        <p class="dis"></p>
        <input type="text" v-model="dataList.bidAmount" /> 
    </div>
    <div class="row">
        <p class="dis"></p>
        <span>{{ dataList.loanUser }}</span>
    </div>
    <div class="row">
        <p class="dis"></p>
        <span>{{ dataList.guaranteeRealm }}</span>
    </div>
    <div class="row">
        <p class="dis"></p>
        <span>{{ dataList.productName }}</span>
    </div>
    <div class="row">
        <p class="dis"></p>
        <span>{{ dataList.months }}  </span>
    </div>
    <div class="row">
        <p class="dis"></p>
        <div>
            <input type="text" v-model="dataList.rate" />%
        </div>
    </div>
    <div class="row">
        <p class="dis"></p>
        <div>
            <input type="text" v-model="dataList.addRate" />%
        </div>
    </div>
    <div class="row">
        <p class="dis"></p>
        <selection :index-default="cusIndex" :arr="customerType" @selected="customerFn"></selection>
    </div>
    <div class="row">
        <p class="dis"></p>
        <span>{{ dataList.method | method }}</span>
    </div>
    <div class="row">
        <p class="dis"></p>
        <p>
            <input type="text" v-model="dataList.minAmount" />
        </p>
    </div>
    <div class="row">
        <p class="dis"></p>
        <p>
            <input type="text" v-model="dataList.maxAmount" />
        </p>
    </div>
    <div class="row">
        <p class="dis"></p>
        <p>
            <input type="text" v-model="dataList.stepAmount" />
        </p>
    </div>
    <div class="row">
        <p class="dis"></p>
        <p>
            <input type="text" v-model="dataList.loanGuaranteeFee" />%
        </p>
    </div>
    <div class="row">
        <p class="dis"></p>
        <p>
            <input type="text" v-model="dataList.loanManageFee" />%
        </p>
    </div>
    <div class="row">
        <p class="dis"></p>
        <p>
            <input type="text" v-model="dataList.loanServiceFee" />%
        </p>
    </div>
    <div class="row">
        <p class="dis"></p>
        <p>
            <input type="text" v-model="dataList.loanInterestFee" />%
        </p>
    </div>
    <div class="row">
        <p class="dis"></p>
        <p>
            <input type="text" v-model="dataList.loanRiskFee" />%
        </p>
    </div>
    <div class="row">
        <p class="dis"></p>
        <p>
            <input type="text" v-model="dataList.investInterestFee" />%
        </p>
    </div>
    <div class="row">
        <p class="dis"></p>
        <span>{{ dataList.firmInfo }}</span>
    </div>
    <div class="row">
        <p class="dis"></p>
        <span>{{ dataList.operationRange }}</span>
    </div>
    <div class="row">
        <p class="dis"></p>
        <span>{{ dataList.description }}</span>
    </div>
    <div class="row">
        <p class="dis"></p>
        <span>{{ dataList.repaySource }}</span>
    </div>
    <div class="row">
        <p class="dis"></p>
        <span>{{ dataList.riskInfo }}</span>
    </div>
    <div class="" >
        <div class="row">
            <p class="dis"></p>
        </div>
        <div v-if="dataList.legalPersonPhotoUrl" class="photoBox">
            <img v-for="(item, index) in legalPersonImg" :src="item" alt="">
        </div>
    </div>
    <div class="">
        <div class="row">
            <p class="dis"></p>
        </div>
        <div v-if="dataList.enterpriseInfoPhotoUrl" class="photoBox">
            <img v-for="(item, index) in enterpriseInfoImg" :src="item" alt="">
        </div>
    </div>
    <div class="">
        <div class="row">
            <p class="dis"></p>
        </div>
        <div v-if="dataList.contractPhotoUrl" class="photoBox">
            <img v-for="(item, index) in contractImg" :src="item" alt="">
        </div>
    </div>
    <div class="">
        <div class="row">
            <p class="dis"></p>
        </div>
        <div v-if="dataList.assetsPhotoUrl" class="photoBox">
            <img v-for="(item, index) in assetsImg" :src="item" alt="">
        </div>
    </div>
    <div class="">
        <div class="row">
            <p class="dis"></p>
        </div>
        <div v-if="dataList.othersPhotoUrl" class="photoBox">
            <img v-for="(item, index) in othersImg" :src="item" alt="">
        </div>
    </div>
    <p class="sub">
        <span @click="saveFn" class="subAll"></span>
    </p>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import selection from '../submodel/selection.vue'
import alert from '../submodel/alert.vue'
export default {
    name: 'new-bid',
    props: {
        editData: {
            type: Object,
            default: function () {
                return {}
            }
        },
        flag: {
            type: String,
            default: '1'
        }
    },
    data () {
        return {
            title: '',
            customerType: [
                {
                    lable: '',
                    val: 1
                },
                {
                    lable: '',
                    val: 0
                }
            ],
            cusIndex: 0,
            dataList: {
                id: '',    /* id  */
                title: '',   /*  */
                serial: '',   /*  */
                amount: '',   /*  */
                surplusAmount: '',   /*  */
                bidAmount: '',   /*  */
                loanUser: '',   /*  */
                loanUserId: '',   /* ID */
                guaranteeRealm: '',   /*  */
                guaranteeId: '',   /* id */
                productName: '',  /*  */
                productId: '',   /* id */
                months: '',   /*  */
                rate: '',   /*  */
                addRate: '',  /*  */
                groupId: '',  /*  */
                method: '',   /*  */
                minAmount: '100',  /*  */
                maxAmount: '',  /*  */
                stepAmount: '100',  /*  */
                loanGuaranteeFee: '',   /*  */
                loanManageFee: '',   /*  */
                loanServiceFee: '',   /*  */
                loanInterestFee: '',   /*  */
                loanRiskFee: '',   /*  */
                investInterestFee: '',   /*  */
                firmInfo: '',   /*  */
                operationRange: '',     /*  */
                description: '',     /*  */
                repaySource: '',     /*  */
                riskInfo: '',     /*  */
                urlLegalPerson: [],     /*  */
                urlCompany: [],     /*  */
                urlContract: [],     /*  */
                urlAssets: [],     /*  */
                urlOthers: []      /*  */
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    created () {
        this.$nextTick(() => {
            let This = this
            if (this.flag === '2') {
                this.title = ''
                this.$ajax('/back/loan/searchLoanByParams', {
                    loan: {
                        id: this.editData.id
                    }
                }, function (res) {
                    let loanData = res.data.loanDetail
                    This.dataList = loanData.loan
                    This.dataList.loanUser = loanData.loan.loanUserName
                    This.dataList.amount = loanData.projectDtail.project.amount
                    This.dataList.surplusAmount = loanData.projectDtail.project.surplusAmount
                    This.dataList.addRate = loanData.loan.addRate || ''
                    This.dataList.bidAmount = This.editData.amount
                }, function (err) {
                    console.log(err)
                })
            } else {
                this.title = ''
                this.dataList = this.editData
                this.dataList.loanUser = this.editData.name
                this.dataList.productName = this.editData.productName || ''
                this.dataList.addRate = this.editData.addRate || ''
            }
        })
    },
    components: {
        selection,
        alert
    },
    methods: {
        customerFn (index) {
            this.cusIndex = index
        },
        goback () {
            this.$emit('back')
        },
        saveFn () {
            let This = this
            let reg = /^\+?[1-9][0-9]*$/
            if (!this.dataList.title || !this.dataList.serial || !this.dataList.bidAmount || !this.dataList.rate || !this.dataList.minAmount || !this.dataList.maxAmount || !this.dataList.stepAmount || !reg.test(this.dataList.bidAmount) || parseInt(this.dataList.bidAmount) > parseInt(this.dataList.surplusAmount)) {
                if (!this.dataList.title) {
                    this.msg.content = ''
                } else if (!this.dataList.serial) {
                    this.msg.content = ''
                } else if (!this.dataList.bidAmount) {
                    this.msg.content = ''
                } else if (!this.dataList.rate) {
                    this.msg.content = ''
                } else if (!this.dataList.minAmount) {
                    this.msg.content = ''
                } else if (!this.dataList.maxAmount) {
                    this.msg.content = ''
                } else if (!this.dataList.stepAmount) {
                    this.msg.content = ''
                } else if (!reg.test(this.dataList.bidAmount)) {
                    this.msg.content = ''
                } else if (parseInt(this.dataList.bidAmount) > parseInt(this.dataList.surplusAmount)) {
                    this.msg.content = ''
                }
                this.msg.time = '1500'
                this.msg.isShow = true
                return
            }
            if (this.flag === '2') {
                this.$ajax('/back/loan/modifyUnpublishedLoan', {
                    loan: {
                        'id': this.dataList.id,
                        'title': this.dataList.title,
                        'serial': this.dataList.serial,
                        'loanUserId': this.dataList.loanUserId,
                        'guaranteeRealm': this.dataList.guaranteeRealm,
                        'guaranteeId': this.dataList.guaranteeId,
                        'productId': this.dataList.productId,
                        'productName': this.dataList.productName,
                        'amount': this.dataList.bidAmount,
                        'months': this.dataList.months,
                        'rate': this.dataList.rate,
                        'addRate': this.dataList.addRate,
                        'groupId': this.customerType[this.cusIndex].val,
                        'method': this.dataList.method,
                        'minAmount': this.dataList.minAmount,
                        'maxAmount': this.dataList.maxAmount,
                        'stepAmount': this.dataList.stepAmount,
                        'loanGuaranteeFee': this.dataList.loanGuaranteeFee,
                        'loanManageFee': this.dataList.loanManageFee,
                        'loanServiceFee': this.dataList.loanServiceFee,
                        'loanInterestFee': this.dataList.loanInterestFee,
                        'loanRiskFee': this.dataList.loanRiskFee,
                        'investInterestFee': this.dataList.investInterestFee
                    }
                }, function (data) {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                    if (data.data.message.code === 1000) {
                        setTimeout(() => {
                            This.$emit('back')
                        }, 1500)
                    }
                }, function (err) {
                    console.log(err)
                })
            } else {
                this.$ajax('/back/loan/bidApply', {
                    loan: {
                        'projectId': this.dataList.id,
                        'title': this.dataList.title,
                        'serial': this.dataList.serial,
                        'loanUserId': this.dataList.loanUserId,
                        'guaranteeRealm': this.dataList.guaranteeRealm,
                        'guaranteeId': this.dataList.guaranteeId,
                        'productId': this.dataList.productId,
                        'productName': this.dataList.productName,
                        'amount': this.dataList.bidAmount,
                        'months': this.dataList.months,
                        'rate': this.dataList.rate,
                        'addRate': this.dataList.addRate,
                        'groupId': this.customerType[this.cusIndex].val,
                        'method': this.dataList.method,
                        'minAmount': this.dataList.minAmount,
                        'maxAmount': this.dataList.maxAmount,
                        'stepAmount': this.dataList.stepAmount,
                        'loanGuaranteeFee': this.dataList.loanGuaranteeFee,
                        'loanManageFee': this.dataList.loanManageFee,
                        'loanServiceFee': this.dataList.loanServiceFee,
                        'loanInterestFee': this.dataList.loanInterestFee,
                        'loanRiskFee': this.dataList.loanRiskFee,
                        'investInterestFee': this.dataList.investInterestFee
                    }
                }, function (data) {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                    if (data.data.message.code === 1000) {
                        setTimeout(() => {
                            This.$emit('back')
                        }, 1500)
                    }
                }, function (err) {
                    console.log(err)
                })
            }
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    computed: {
        legalPersonImg: function () {
            return this.dataList.legalPersonPhotoUrl.split(',')
        },
        enterpriseInfoImg () {
            return this.dataList.enterpriseInfoPhotoUrl.split(',')
        },
        contractImg () {
            return this.dataList.contractPhotoUrl.split(',')
        },
        assetsImg () {
            return this.dataList.assetsPhotoUrl.split(',')
        },
        othersImg () {
            return this.dataList.othersPhotoUrl.split(',')
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
h3 {
    text-align: center;
    font-weight: normal;
}
.backpage {
    color: #5ea7ea;
    cursor: pointer;
}
.row {
    width: 650px;
    margin: 0 auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #666;
    .dis {
        text-align: right;
        width: 200px;
    }
}
#month {
    width: 60px;
}
label, input {
    vertical-align: middle;
}
.sub {
    padding-bottom: 30px;
    text-align: center;
    span {
        display: inline-block;
        padding: 6px 30px;
        background-color: #38afff;
        border-radius: 4px;
        color: #fff;
        cursor: pointer;
        text-align: center;
    }
    .resAll {
        background-color: red;
    }
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
.btn-primary {
    padding: 6px 20px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    vertical-align: middle;
}
.photoBox {
    text-align: center;
    img {
        width: 300px;
    }
}
</style>



